import React, {Component} from 'react'
import Logoimg from '../images/logo.png'

class Logo extends Component{
  render(){
    return(
      <img src={Logoimg} alt='LogoImage'  width="140px"  className="logo-img"/>
    )
  }
}


class Navbar extends Component{
  render(){
    return(
      <nav className="navbar navbar-expand-lg navbar-light">
      <a className="navbar-brand" href="#"> <Logo /> </a>
      <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon" />
      </button>
      <div className="collapse navbar-collapse" id="navbarNavDropdown">
        <ul className="navbar-nav">
          <li className="nav-item active">
            <a className="nav-link" href=""> <i class="fas fa-home nav-icon"></i> <span className="sr-only">(current)</span></a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="https://oladoc.com/pakistan/karachi/cardiac-surgeon" target="block"><i class="fas fa-heartbeat nav-icon"></i> </a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="https://oladoc.com/pakistan/karachi/skin-specialist" target="block"> <i class="fas fa-allergies nav-icon"></i></a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="https://oladoc.com/pakistan/karachi/child-specialist" target="block"><i class="fas fa-child nav-icon"></i></a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="https://oladoc.com/pakistan/karachi/treatment/teeth-cleaning"> <i class="fas fa-tooth nav-icon"></i></a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="https://oladoc.com/pakistan/karachi/eye-specialist" target="block">   <i class="fas fa-eye nav-icon"></i></a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="https://oladoc.com/pakistan/karachi/pulmonologist"><i class="fas fa-lungs nav-icon"></i> </a>
          </li>
           <li className="nav-item"> 
         <h5 className="partner">Partnership with:</h5>

         <a ></a> <img className="nav-power-img"  src="data:image/png;base64," alt="..." />
          
          </li> 
        </ul>
      </div>
    </nav>
  );
  }
}
export {
  Navbar,Logo
};


  
   